























var util = require('../../../common/util.js');
var formatLocation = util.formatLocation;

export default {
	data() {
		return {
			title: 'getLocation',
			hasLocation: false,
			location: {}
		}
	},
	methods: {
		getLocation: function () {
			uni.getLocation({
				success: (res) => {
					this.hasLocation = true,
						this.location = formatLocation(res.longitude, res.latitude)
				}
			})
		},
		clear: function () {
			this.hasLocation = false
		}
	}
}
